@charset "UTF-8";
.me .keshi {
  width: 98%;
  box-shadow: 0px 0px 6px #fdfdfd;
  margin: 0px auto; }

.me .jihua {
  background: linear-gradient(#f5d520, #d7f77d, #f0da5f);
  /* 标准的语法 */ }

.me .jihua h4 {
  border-radius: 50%;
  box-shadow: 0px 0px 10px #fff;
  width: 160px;
  display: inline-block;
  height: 160px;
  text-align: center;
  line-height: 160px;
  font-size: 30px;
  color: #fff;
  margin: 10px;
  font-style: italic;
  letter-spacing: 4px;
  font-family: "华文琥珀";
  background: linear-gradient(#f5d520, #d7f77d, #f0da5f);
  /* 标准的语法 */ }

.me .jihua h5 {
  border-radius: 10px;
  margin-right: 20px;
  padding: 5px;
  text-align: center;
  text-shadow: 0px 0px 10px rgba(165, 163, 163, 0.116);
  letter-spacing: 4px;
  font-family: "华文琥珀";
  font-style: italic;
  font-size: 30px;
  color: #fff; }

